*{box-sizing:border-box}.container{display:flex;flex-direction:column}.container main{flex:1 1;display:flex;justify-content:center;align-items:center}.container main .search-box{margin:0 auto;width:360px;padding:0 2%}.container main .search-box h3{margin:0;text-align:center;opacity:0.8}.container main .search-box form{margin-top:30px;display:flex;flex-direction:column;padding:0 2%}.container main .search-box form input{padding:5px 20px;font-size:13px;text-align:center;border:1px solid rgba(0,0,0,0.12);border-radius:5px;opacity:0.7}.container main .search-box form input:focus{outline:none;border:1px solid #a29bfe}.container main .search-box form input:hover{border:1px solid #a29bfe}.container main .search-box form button{margin:0 auto;margin-top:20px;width:80px;background-color:#a29bfed8;border:none;border-radius:5px;padding:5px 0;color:white}.container main .search-box form button:hover{background-color:#a29bfe}

